import {Component, Inject, LOCALE_ID, ViewChild} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CalendarComponent} from "ionic2-calendar/calendar";
import * as moment from "moment";
import {CalendarEvent} from "../../model/calendarEvent";
import { Corso } from '../../model/corso';
import { dataLoader } from '../../model/dataLoader';
import { coursesListAtDateX } from '../../model/calendarDataHandler';

/**
* Generated class for the CalendarPage page.
*
* See https://ionicframework.com/docs/components/#navigation for more info on
* Ionic pages and navigation.
*/

@IonicPage()
@Component({
	selector: 'page-calendar',
	templateUrl: 'calendar.html',
})
export class CalendarPage {

	coursesAPIData: Array<Corso>;

	eventSource;
	viewTitle;
	calendar = {
		noEventsLabel: 'Nessun evento',
		mode: 'month',
		currentDate: moment().toDate(),
		currentSection: moment().format('MMMM'),
		locale: this.locale
	};

	@ViewChild(CalendarComponent) myCal: CalendarComponent;

	constructor(public navCtrl: NavController, @Inject(LOCALE_ID) private locale:string, private alertCtrl: AlertController) {
		this.eventSource = [];

		/* Load courses data from external module created <dataLoader> */
		dataLoader()
			.then((loadedCourses)=> {
				console.log(loadedCourses);
				this.coursesAPIData = loadedCourses;
				console.log(this.coursesAPIData);
			});
	}


	// Change current month/week/day
	next() {
		var swiper = document.querySelector('.swiper-container')['swiper'];
		swiper.slideNext();
	}

	back() {
		var swiper = document.querySelector('.swiper-container')['swiper'];
		swiper.slidePrev();
	}

	// Change between month/week/day
	changeMode(mode) {
		this.calendar.mode = mode;
	}

	// Focus today
	today() {
		this.calendar.currentDate = moment().toDate();
	}

	// Selected date reange and hence title changed
	onViewTitleChanged(title) {
		this.viewTitle = title;
	}

	// Calendar event was clicked
	async onEventSelected(event) {
		console.log('event selected');
	}

	// Time slot was clicked
	onTimeSelected(ev) {
		console.log('time selcted');
	}

	loadEvents() {
		console.log('events loaded');
		this.eventSource = this.eventSourceLoader();
	}

	onCurrentDateChanged = (ev: Date) => {
		console.log('selected date: ' + ev);
	}

	eventSourceLoader(){
		let events = [];
		/*
		for(let elem of this.coursesAPIData){
			events.push({
				title: elem.courseTitle,
				startTime: moment(`${elem.lessonDate} ${elem.lessonTimeStart}`).format(),
				endTime: moment(`${elem.lessonDate} ${elem.lessonTimeEnd}`).format(),
				allDay: false
			});
		}
		*/
		//var inputDateStart = new Date(`2019-02-20T13:00:00Z`);
		//var inputDateEnd = new Date(`2019-02-20T15:00:00Z`);


		//console.log(inputDateStart);
		//console.log(inputDateEnd);


		for(let elem of this.coursesAPIData){
			var inputDateStart = new Date(`${elem.lessonDate}T${elem.lessonTimeStart}Z`);
			var inputDateEnd = new Date(`${elem.lessonDate}T${elem.lessonTimeEnd}Z`);
			events.push({
				title: elem.courseTitle,
				startTime: new Date(Date.UTC(inputDateStart.getFullYear(), inputDateStart.getUTCMonth(), inputDateStart.getUTCDay())),
				endTime: new Date(Date.UTC(inputDateEnd.getFullYear(), inputDateEnd.getUTCMonth(), inputDateEnd.getUTCDay())),
				allDay: false
			});
		}
		console.log(events);

		/*
		events.push({
			title: 'Titolo Evento',
			startTime: new Date(Date.UTC(inputDateStart.getFullYear(), inputDateStart.getUTCMonth(), inputDateStart.getUTCDay())),
			endTime:  new Date(Date.UTC(inputDateEnd.getFullYear(), inputDateEnd.getUTCMonth(), inputDateEnd.getUTCDay())),
			allDay: false
		});
		*/
		return events;
	}

}



